.fist2 {
  -webkit-animation-name: fist-pulse;
          animation-name: fist-pulse;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  height: 200px;
  left: 200px;
  position: absolute;
  top: 240px;
  width: 200px;
  z-index: 4;
}

@-webkit-keyframes fist-pulse {
  0% {
    left: 250px;
    position: absolute;
    top: 290px;
    height: 100px;
    width: 100px;
  }

  100% {
    left: 200px;
    position: absolute;
    top: 240px;
    height: 200px;
    width: 200px;
  }
}

@keyframes fist-pulse {
  0% {
    left: 250px;
    position: absolute;
    top: 290px;
    height: 100px;
    width: 100px;
  }

  100% {
    left: 200px;
    position: absolute;
    top: 240px;
    height: 200px;
    width: 200px;
  }
}

.star2 {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-play-state: running;
